<template>
    <div>
        <demo-header></demo-header>
        <hr/>
        <div>
            用户名：<input type="text" v-model="loginInfo.name" placeholder="用户名"/>
            <br/>
            密码：<input type="password" v-model="loginInfo.password" placeholder="请输入密码"/>
            <br/>
            <button v-on:click="login">登录</button>
            <br/>
            登录情况验证信息：<textarea cols="30" rows="10" v-model="responseResult"></textarea>
        </div>
        <hr/>
        <demo-footer></demo-footer>
    </div>
</template>
<script>
import demoHeader from '@/components/common/DemoHeader'
import demoFooter from '@/components/common/DemoFooter'

export default {
    name:'DemoLogin',
    //这玩意是吧一个页面当成另一个页面的组件声明，然后再页面里使用
    components:{demoHeader,demoFooter},
    data(){
        return{
            loginInfo:{username:'',password:''},
            responseResult:[]
        }
    },
    methods:{
        login(){
            this.$axios
            .post('/login/login.json',{
                username:this.loginInfo.username,
                password:this.loginInfo.password
            })
            .then(successResponse => {
                this.responseResult = JSON.stringify(successResponse.data)
                if(successResponse.data.code ==200){
                    this.$router.replace({path:'/index'})
                }
            })
            .catch(failResponse =>{

            })
        }
    }

}
</script>


